.container-row
    min-height: 80vh
    padding: 50px 0
    //width: 100%
    &:nth-child(odd)
        background-color: #f7f7f7
    &:nth-child(even)
        background: #fff
    .ant-space-item
        margin: 0 auto
        //width: 80%
        text-align: center
        .ant-typography
            margin: 0 auto
            max-width: 600px
    #testimonials
        h2
            margin-bottom: 24px
        .ant-carousel
            .slick-slide
                display: flex
                justify: center
                align: middle
                .ant-card
                    height: 300px
                    padding: 40px
                    flex-flow: wrap
                    h3, h4
                        width: 100%
                    h3
                        text-align: center
            .slick-dots li 
                button
                    background: #000 !important
                &.slick-active
                    button
                        background: #000 !important
        
